body{
  background-color: rgb(23, 27, 34);
}

.app-wrapper{
  margin: 0 auto;
  display: grid;
  width: 1200px;

  grid-template-areas: 
  "h h"
  "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  background-color: rgb(23, 27, 34);
}

.app-wrapper-content{
  grid-area: c;
}
