.header{
  grid-area: h;
  background-color: rgb(58, 64, 71);
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  border: .1px solid rgb(83, 89, 105);
}
  
.icon{
  height: 35px;
  margin-left: 15px;
  margin-top: 12px;
  cursor: pointer;
}

.loginBlock{
  float: right;
  margin-top: 18px;
  margin-right: 15px;
  color: white;
}

.loginBlock a {
  color: white;
}

.login,
.logout{
    background-color: rgb(48, 53, 66);
    border-radius: 5px;
    padding: 5px 10px;
    border: .1px solid rgb(48, 53, 66);
    color: white;
    width: 75px;
    height: 30px;
    transition: all .4s ease;
    -webkit-transition: all ease .4s;
    text-decoration: none;
    font-size: 16px;
}

.login:hover,
.logout:hover{
  background-color: rgb(56, 62, 76);
}

.icon:hover{
  cursor: pointer;
}